.header {
  display: flex;
  justify-content: center;
  width: 100%;
  min-height: 84px;
  background-color: var(--white);
  padding: 0 20px;
}

.wrapper {
  position: relative;
  max-width: 1116px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 696px;
  width: 100%;
}
